body {
  max-width: 1440px;
  position: relative;
  background-image: url(../img/back.png);
  background-repeat: no-repeat;
  height: 100vh;
  background-size: inherit;
  max-width: 100%;
  max-width: 100%;
}

.container {
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 15px;

  @media(max-width: 1200px) {
    .container {
      max-width: 970px;
    }
  }

  @media(max-width: 992px) {
    .container {
      max-width: 750px;
    }
  }

  @media(max-width:767px) {
    .container {
      max-width: none;
    }
  }
}

section.wrap {
  position: relative;

  .limited_wrapper {
    position: relative;
    display: flex;

    .left_col {
      flex: 0 0 50%;
      display: flex;
    }
  }
}

.horizontal_num {
  -webkit-writing-mode: vertical-rl;
  -ms-writing-mode: tb-rl;
  writing-mode: vertical-rl;
  right: 140px;
  top: 56px;
  position: absolute;

  span {
    font-size: 28px;
    font-family: "Geometria";
    font-weight: 800;
    font-style: normal;
  }
}

.horizontal_text {
  margin-left: 107px;
  margin-top: 56px;
  writing-mode: vertical-rl;
  text-orientation: upright;

  p {
    font-size: 9px;
    font-family: "Geometria";
    font-weight: 200;
    font-style: italic;
  }
}

.square {
  width: 180px;

  img {
    width: 100%;
  }
}

.giometric_symbols {
  width: 310px;
  margin-left: 180px;
  margin-top: 52px;

  img {
    width: 100%;
  }
}

.navbar {
  margin-top: 90px;
}

.navbar-button {
  position: relative;
  display: block;
  margin-top: 42px;
}

.button {
  cursor: pointer;
  position: relative;

  i {
    font-size: 22px;
    position: absolute;
    right: -164px;
    top: 1px;
  }
}

.button:before {
  content: url(../img/button.png);
  position: absolute;
  right: -181px;
  top: -15px;
}

.overlay {
  position: fixed;
  background: #f7e611;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.35s, visibility 0.35s, height 0.35s;
  overflow: hidden;
  z-index: 10000;
}

.overlay.open {
  opacity: 1;
  visibility: visible;
  height: 100%;
}

.close-menu {
  display: block;
}

.overlay.open {
  ul {
    text-align: center;
    flex-direction: column;
    padding: 0;
    margin: 190px auto;
    height: 100%;
    width: 100%;

    li {
      padding-bottom: 20px;

      a {
        text-decoration: none;
      }
    }
  }
}

.top_gio {
  width: 224px;
  position: absolute;
  z-index: 15;
  right: 264px;
  top: 75px;
}